import { z } from 'zod';

import { BaseConnectorConfig, ConfiguredConnectorTypes } from '../../types';
import { ConnectorOrigin } from '../type-enums';
import { ActionInterface, ActionSchema } from '../workflow';

export const ConnectorSchema = z.object({
  identifier: z.string(),
  connectorType: z.nativeEnum(ConfiguredConnectorTypes),
  name: z.string(),
  location: z.string(),
  config: z.record(z.any()),
  enabled: z.boolean(),
  actions: z.array(ActionSchema).optional(),
  createdAt: z.date().optional(),
  updatedAt: z.date().optional(),
});

export interface ConnectorInterface<T = Partial<BaseConnectorConfig>> {
  identifier: string;
  connectorType: ConfiguredConnectorTypes;
  tenantIdentifier: string;
  name: string;
  location: string;
  config?: T;
  enabled: boolean;
  actions?: Array<ActionInterface>;
  mode?: ConnectorOrigin;
  createdAt?: Date;
  updatedAt?: Date;
}

export type CreateConnectorInterface<T = Partial<BaseConnectorConfig>> = Omit<
  ConnectorInterface<T>,
  'id' | 'createdAt' | 'updatedAt' | 'mode'
>;

export type UpdateConnectorInterface<T = Partial<BaseConnectorConfig>> =
  Partial<
    Omit<
      ConnectorInterface<T>,
      'id' | 'tenantIdentifier' | 'identifier' | 'createdAt' | 'mode'
    >
  >;
